import { createNamespacedHelpers } from 'vuex';
const { mapState, mapMutations } = createNamespacedHelpers('storeCommodity');

export default {
	data() {
		return {
			...mapState(["isWrapShow"]),
			isAuth:false,
			userInfoObj:{},
			orderList:[],
			order_status:[
				{img_url:`${this.$imgUrl}/images/myct_indentl_grey__01.png`,text:"待付款",state:1,num:0},
				{img_url:`${this.$imgUrl}/images/myct_indentl_grey__03.png`,text:"待发货",state:2,num:0},
				{img_url:`${this.$imgUrl}/images/myct_indentl_grey__05.png`,text:"待收货",state:3,num:0},
				{img_url:`${this.$imgUrl}/images/myct_indentl_grey__07.png`,text:"待评价",state:4,num:0},
				{img_url:`${this.$imgUrl}/images/myct_indentl_grey__09.png`,text:"退换货",state:5,num:0}
			],
			my_wallet_item:[
				{num:"--",text:"余额"},
				{num:0,text:"积分"},
				{num:0,text:"优惠卷"},
				{num:0,text:"银行卡管理"},
			],
			other_functions:[
				{img_url:`${this.$imgUrl}/images/myct_others_i_red_01.png`,txt:"商品收藏"},
				{img_url:`${this.$imgUrl}/images/myct_others_i_red_13.png`,txt:"拼团频道"},
				{img_url:`${this.$imgUrl}/images/myct_others_i_red_19.png`,txt:"客服"},
				{img_url:`${this.$imgUrl}/images/myct_others_i_red_06.png`,txt:"帮助中心"},

			],
			employeeFunction:[
				{img_url:`${this.$imgUrl}/images/icon-partner-red-pic1.png`,text:"店铺列表",url:"/pages/mys/storelist/storelist"},
				{img_url:`${this.$imgUrl}/images/icon-salesman-red-pic2.png`,text:"品牌审核",url:"/pages/mys/storebrandlist/storebrandlist"},
				{img_url:`${this.$imgUrl}/images/icon-salesman-red-pic3.png`,text:"店铺榜单",url:"/pages/mys/storetop/storetop"},
				{img_url:`${this.$imgUrl}/images/icon-salesman-red-pic4.png`,text:"邀请入驻",url:"/pages/shares/index/index"},
			],
			grade:"",
			salesmanData:{
				kprice: 0,
				month_price: 0,
				shop_num: 0,
				today_num: 0,
				today_price: 0,
				total_price: 0,
				week_price: 0,
			},
			avatar:"",
		};
	},
	onShow() {
		// 页面鉴权
		this.navToLogin();
	},
	computed: {
		...mapState(['userInfo']),
		dataStatistics(){
			return [
				{num:this.salesmanData.shop_num,text:"店铺数量"},
				{num:this.salesmanData.today_price,text:"今日销售额"},
				{num:this.salesmanData.week_price,text:"近7天销售额"},
				{num:this.salesmanData.month_price,text:"近30天销售额"},
			]
		},
		todayDeal(){
			return [
				{num:this.salesmanData.today_num,text:"今日成交件数"},
				{num:this.salesmanData.kprice,text:"今日客单价"},
			]
		}
	},
	methods: {
		...mapMutations(["changeWrapShow"]),
		// 跳转到订单状态页面
		navTo(obj) {
			uni.navigateTo({
				url:`/pages/mys/order/order?state=${obj.state}`
			});
		},
		// 跳转到用户信息页面
		jumpLogin(){
			// 普通用户跳转页面
			if(this.grade === 'VIP1'){
				uni.navigateTo({
					url:'/pages/mys/info/info?userInfo',
				})
			}else{
				// 员工页面
				uni.navigateTo({
					url:'/pages/mys/salesmaninfo/salesmaninfo'
				})
			}
		},
		// 设置图标数量
		iconNum(){
			if(this.orderList){
				for (let i = 0; i < this.orderList.length; i++) {
					const element = this.orderList[i];
					for (let k = 0; k < this.order_status.length; k++) {
						const element2 = this.order_status[k];
						if(element.status === element2.state){
							this.$set(this.order_status[k],"num",element.num)
						}
					}
				}
			}
		},
		navToLogin(){
			const TOKEN = uni.getStorageSync('access_token');
			if (!TOKEN){
				if(this.isWrapShow()){
					uni.switchTab({
						url:"/pages/indexs/index/index",
						success:()=>{
							this.changeWrapShow({"isWrapShow":false})
						}
					})
				}else{
					// 没有登录直接到登录页面
					uni.navigateTo({
						url:"/pages/mys/login/login",
						success:()=>{
							// 到登录页面后显示user页面
							this.changeWrapShow({"isWrapShow":true})
						}
					})
				}
			}else{
				// 如果登录了直接显示user页面
				this.changeWrapShow({"isWrapShow":true})
				// 开始请求用户数据数据
				this.reqUserInfo();
			}
		},
		async reqUserInfo(){
			let checkoutUserResult = await this.checkoutUser();
			if(checkoutUserResult){this.reqUserInfoCore()};
		},
		reqUserInfoCore(){
			// 获取用户信息
			this.$request.GET({
				url:this.$api.apiUrl.GET_USER_INFO
			}).then(res=>{
				let result = res.data;
				if(result.rule !== 'salesman'){
					// 如果是合伙人登录显示入口
					if(result.type === 1 && this.other_functions.length===4){
						this.other_functions.push({
							img_url:`${this.$imgUrl}/images/myct_others_i_red_21.png`,
							txt:"合伙人后台"
						})
					}
					uni.setStorageSync('storage_salesman','')
					// 这是普通用户或者合伙人登录请求的数据;
					this.reqUserOrder();
				}else{
					this.$request.GET({
						url:this.$api.apiUrl.GET_V6_USER_INFO,
					}).then(res=>{
						let result = res.data;
						if(res.code === 'SECCUSS'){
							// 有nickname就用没有就用手机号代替
							if(result.nickname){
								this.$set(this.userInfoObj,'nickname',result.nickname);
							}else{
								this.$set(this.userInfoObj,'nickname',result.mobile);
							}
							// 有头像就用,没有就用默认头像
							if(result.avatar){
								this.avatar = result.avatar + '?' + Date.now();;
							}else{
								this.avatar = 'https://meizi.manogue.com.cn/static/wap/images/myct_hd.png';
							}
							this.grade = '业务员';
							uni.setStorageSync('storage_salesman','true')
							this.reqSalesmanData()
						}
					})
				}
			})
		},
		// 检查是不是员工且登录状态失效
		checkoutUser(){
			// 先查看是什么用户
			const value = uni.getStorageSync('storage_salesman');
			const TOKEN = uni.getStorageSync('access_token');
			if(value){
				this.$request.POST({
					url:this.$api.apiUrl.POST_V6_USER_CHECKTOKEN,
					data:{
						token:TOKEN
					}
				}).then(res=>{
					if(res.code !== 'SECCUSS'){
						// 如果是员工登录且被顶下线就清除token和登录类别
						uni.setStorageSync('storage_salesman','')
						uni.setStorageSync('access_token','')
						uni.navigateTo({
							url:"/pages/mys/login/login"
						})
					}else{
						this.reqUserInfoCore();
					}
				});
			}else{
				return true;
			}	
		},
		// 获取业务员页面信息
		reqSalesmanData(){
			const TOKEN = uni.getStorageSync('access_token');
			this.$request.GET({
				url:this.$api.apiUrl.GET_V6_SHOP_FINANCE,
				data:{
					token:TOKEN,
				},
			}).then(res=>{
				let result = res.data[0];
				for (const key in result) {
					// 如果有值为null的就显示0
					if(!result[key]){
						this.salesmanData[key] = 0;
					}else{
						this.salesmanData[key] = result[key];
					}
				}
			})
		},
		reqUserOrder(){
			// 普通用户还是合伙人
			this.$request.GET({
				url:this.$api.apiUrl.GET_USER_ORDER
			}).then(res=>{
				this.userInfoObj = res.data.infomation;
				if(res.data.infomation.avatar){
					this.avatar = res.data.infomation.avatar + '?' + Date.now();
				}else{
					this.avatar = 'https://meizi.manogue.com.cn/static/wap/images/myct_hd.png';
				}
				this.grade = 'VIP1';
				this.orderList = res.data.order;
				this.iconNum();
			})
			// 认证是否通过
			this.$request.GET({
				url:this.$api.apiUrl.GET_USER_AUTH_STATUS,
			}).then(res=>{
				if(res.code !== 200){
					this.isAuth = true;
				}else{
					this.isAuth = false;
				}
			})
		},
		toAppointPage(item){
			// console.log(item);
			switch (item) {
				case "客服":
					uni.navigateTo({
						url:"/pages/goodss/wolive/wolive"
					})
					break;
				case "合伙人后台":
					uni.navigateTo({
						url:"/pages/mys/brokeragenew/brokeragenew"
					})
					break
				case "推广有礼":
					uni.navigateTo({
						url:"/pages/shares/index/index"
					})
				default:
					break;
			}
		}
	},
};
